.navbar {
  font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  font-size: 14px;
  background-color: #c0c1c5;
  color: black;
}
.navbar .feature {
  display: none;
}
.navbar .navbar-nav {
  padding-left: 24px !important;
  display: flex;
  flex-wrap: wrap;
}
.navbar .navbar-nav .nav-link {
  margin-right: 16px;
  padding: 0 4px !important;
}
.navbar .navbar-nav .nav-link span {
  margin: 0 4px;
}
.navbar button {
  margin-left: 80%;
}

/*---NAV-MOBILE*/
.nav_mobile {
  position: fixed;
  top: 56px;
  right: 0;
  width: 100%;
  bottom: 61%;
  background-color: #ececec;
  transform: translateX(100%);
  opacity: 0;
  transition: transform linear 0.2s, opacity linear 0.2s;
}
.nav_mobile .nav_mobile-list {
  margin-bottom: 0px;
  padding-left: 0px;
  display: flex;
  flex-direction: column;
  /*li{
      display: flex;
      flex-direction: column;
  }*/
}
.nav_mobile .nav_mobile-list .nav_mobile-link {
  font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.9);
  display: block;
  padding: 8px 0;
  padding-left: 20px;
}
.nav_mobile .nav_mobile-list .nav_mobile-link i {
  width: 14px;
  height: 14px;
}
.nav_mobile .nav_mobile-list .nav_mobile-link:hover {
  background-color: #8f8d8d;
}

.nav_input:checked ~ .nav_mobile {
  transform: translateX(0%);
  opacity: 1;
}

@media (max-width: 768px) {
  .navbar {
    display: none;
  }

  .nav_mobile {
    z-index: 1;
  }
}
section {
  padding: 40px 12px 40px 75px !important;
  font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  font-size: 28px;
  color: rgba(0, 0, 0, 0.55);
}

i {
  font-size: 14px;
  margin: 0 4px;
}

img {
  width: 16px;
  height: 15px;
  margin: 0 4px;
}

@media (max-width: 768px) {
  section {
    padding: 5px 15px 5px 10px !important;
    font-size: 20px;
    margin: 10px 0 10px 0;
  }
}
.ms-Icon--ChevronDown {
  margin: 1px 4px 0px 6px;
  font-size: 12px;
}

.aside-pc {
  font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  font-size: 14px;
  display: flex;
}
.aside-pc .container-fluid {
  margin-left: 73px;
  margin-right: 1px !important;
}
.aside-pc .container-fluid .row {
  padding: 8px 8px;
}
.aside-pc .container-fluid .row .icon {
  width: 40px !important;
}
.aside-pc .container-fluid .row .name {
  width: 370px !important;
}
.aside-pc .container-fluid .row .space {
  width: 40px !important;
}
.aside-pc .container-fluid .row .modify {
  width: 170px !important;
}
.aside-pc .container-fluid .row .flex-box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.aside-pc .container-fluid #row-1 {
  border-bottom: 1px solid #c0c1c5;
}
.aside-pc .container-fluid #row-2, .aside-pc .container-fluid #row-6, .aside-pc .container-fluid #row-5, .aside-pc .container-fluid #row-4, .aside-pc .container-fluid #row-3 {
  border-bottom: 1px solid #e3e4e7;
  padding: 15px 8px 15px 8px;
}
.aside-pc .container-fluid #row-2 .modify span, .aside-pc .container-fluid #row-6 .modify span, .aside-pc .container-fluid #row-5 .modify span, .aside-pc .container-fluid #row-4 .modify span, .aside-pc .container-fluid #row-3 .modify span {
  color: #7b7a78;
}
/*-----ASIDE MOBILE-----*/
.aside-mobile {
  display: none;
}

@media (max-width: 768px) {
  .aside-pc {
    display: none;
  }

  .aside-mobile {
    z-index: -1;
    display: flex;
    font-size: 10px;
    flex-direction: column;
  }
  .aside-mobile .span-title {
    font-weight: 700;
    font-style: italic;
  }
  .aside-mobile .card {
    margin: 0 5px 15px 5px;
  }
  .aside-mobile .card .card-item-header {
    margin-top: 0;
    border: 1px solid #c0c1c5;
    padding: 8px 0 8px 5px;
    background-color: #dbdcdc;
    display: flex;
  }
  .aside-mobile .card .card-item-header img {
    margin: 0 4px 2px 82%;
  }
  .aside-mobile .card .card-item {
    border-left: 1px solid #c0c1c5;
    border-bottom: 1px solid #c0c1c5;
    margin-left: 0.1px;
    margin-right: 0.8px;
    padding: 8px 10px 8px 6px !important;
  }
}